/* Body */
body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  width: 100%;
  /* Section */
  /* min-width < 900px + 1px and max-width < 1120px */
  /* min-width < 550px + 1px and max-width < 900px */
  /* min-width < 350px + 1px and max-width < 550px */
  /* max-width < 350px */
}
body .section {
  max-width: 1440px;
  margin: 6rem 14.375rem;
  display: flex;
  justify-content: center;
  align-items: center;
  /* Content */
  /* min-width > 1120px + 1px */
}
body .section .content {
  display: block;
  /* Title */
  /* Copy */
  /* Button */
}
body .section .content .title {
  /* Body Title */
  line-height: 50px;
  font-weight: 700;
  /* Dark */
  color: #121212;
  /* Fonts */
  font-family: "Roboto", serif;
  font-size: 42px;
}
body .section .content .copy {
  /* Body Copy */
  line-height: 24px;
  font-weight: 300;
  /* Dark */
  color: #121212;
}
body .section .content .button {
  /* Auto layout */
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 1rem 2.5rem;
  margin-top: 2rem;
  /* Primary #1  */
  border: none;
  border-radius: 32px;
  background: #E4C1AE;
  line-height: 16px;
  /* Text */
}
body .section .content .button .button-text {
  font-family: "Roboto", serif;
  font-style: normal;
  line-height: 100%;
  font-weight: 700;
  color: white;
}
